<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <yd-cell-group>
            <yd-cell-item arrow type="label">
                <select slot="right" @change="changelang">
                    <option v-for="item in lang" :value="item.value" :selected="item.selected">{{item.name}}</option>
                </select>
            </yd-cell-item>
            <yd-cell-item arrow type="label">
                <select slot="right" @change="changecoin">
                    <option v-for="item in coin" :value="item.value" :selected="item.selected">{{item.name}}</option>
                </select>
            </yd-cell-item>
            <!-- <yd-cell-item arrow>
                <span slot="left">WEB3设置</span>
            </yd-cell-item> -->
        </yd-cell-group>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
  import footerBar from './footer.vue'
  import {getCookie,setCookie} from '../assets/js/cookie.js'
export default {
  components:{
      "headerBar":headerBar,
      "footerBar":footerBar,
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:this.$t('mycenter.system'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      lang:[
        {name:'中文',selected:false,value:'zh'},
        {name:'English',selected:false,value:'en'},
        {name:'日本語',selected:false,value:'jp'},
        {name:'한글',selected:false,value:'han'}
      ],
      coin:[
        {name:'CNY',selected:false,value:'CNY'},
        {name:'USD',selected:false,value:'USD'},
        {name:'JPY',selected:false,value:'JPY'},
        {name:'KRW',selected:false,value:'KRW'}
      ],
      selectval:'',
    }
  },
  created(){
    this.lang.forEach(function(item){
      if (getCookie("lang") == item.value) {
        item.selected=true
      }
    })
    this.coin.forEach(function(item){
      if (getCookie("coin") == item.value) {
        item.selected=true
      }
    })
  },
  methods:{
    //切换语言
    changelang(ele){
      this.$i18n.locale=ele.target.value
      setCookie("lang",ele.target.value,true)
    },
    //切换货币单位
    changecoin(ele){
      setCookie("coin",ele.target.value,true)
      setCookie("Currency_list","")
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.yd-grids-item span:nth-child(2){
  display: block;
  color: #888
}
.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
  -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.yd-cell-item{
  border-bottom: 1px solid #f4f4f4!important; 
}
.yd-cell-item select{
  font-size: .29rem;
  color: #666;
  letter-spacing: 0;
  margin-left: .03rem;
}
</style>



